<template>
    <div class="imgBox" @click="choseImage">
        <i>{{title}}</i>
        <img :src="img_src" v-if="show" />
    </div>
</template>

<script type="text/javascript">
    import {
        indexActive
    } from "../assets/base64.js"
    
    import { getWxConfig } from "@/service/getData";
    import { setStore,getStore,removeStore } from "@/config/mUtils";
    
    import wx from "weixin-js-sdk";
    
    export default {
        props: ['title' , "backUrl"],
        data() {
            return {
                show:false,
                img_src:"",
                serverId:""
            }
        },
        methods: {
            choseImage:function(){

                var _this = this;
                if(this.show){
                    return;
                }
                wx.ready(function(){
                    wx.chooseImage({
                        count: 1, 
                        sizeType: ['original', 'compressed'],
                        sourceType: ['album', 'camera'],
                        success: function (res) {

                            var localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片

                            _this.show=1;
                            _this.img_src = localIds[0];

                            wx.uploadImage({
                                localId: localIds[0], // 需要上传的图片的本地ID，由chooseImage接口获得
                                isShowProgressTips: 1, // 默认为1，显示进度提示
                                success: function (res) {
                                    
                                    _this.serverId = res.serverId; // 返回图片的服务器端ID

                                    _this.$emit('selectImg', { serverId: res.serverId });
                                }
                            });
                        }
                    });
                });
            }
        },
        mounted: function() {
            
            getWxConfig(this.$store.state.userId , this.backUrl).then(res => {

                wx.config({
                    debug: true, 
                    appId: res.data.app_id, 
                    timestamp: res.data.time,
                    nonceStr: res.data.rand ,
                    signature: res.data.sign ,
                    jsApiList: ['chooseImage','uploadImage','startRecord','stopRecord','onVoiceRecordEnd','uploadVoice'] ,
                });
            })

        }
    }
</script>

<style lang="scss" scoped>
    .imgBox {
        width:100%;
        height: 354px;
        border: 2px dashed #e5e5e5;
        margin-top: 30px;
        position: relative;
        display: flex;
        flex-direction: column;
        overflow:hidden;
        align-items: center;
        background: url("data:image/png;base64,") no-repeat center top 112px;
        background-size: 60px 60px;
        border-radius: 8px;
        
        &:last-child {
            margin-top: 20px;
        }
        input {
            width: 690px;
            height: 354px;
            opacity: 0;
            position: absolute;
        }
        i{
            font-size: 28px;
            padding-top: 214px;
            color: #999;
        }
        img{
            width:100%;
            position: absolute;
            top:0px;
        }
    }
</style>




