<template>
	<div class="map" ref="mapdiv" v-bind:style="{height:divheight.height+'px'}">
		<iframe id="mapPage" width="100%" height="100%" frameborder=0
				src="http://apis.map.qq.com/tools/locpicker?search=1&type=1&key=QTVBZ-U7KR6-7UOSQ-MVPCC-F2BB2-HSFEA&referer=myapp">
		</iframe>
	</div>
</template>
<script>
    import {
        setStore,
        getStore
    } from '../config/mUtils'
    export default {
        name: 'QQMap',
        methods: {
            messageListener (event) {
                // 接收位置信息，用户选择确认位置点后选点组件会触发该事件，回传用户的位置信息
                var loc = event.data
                if (loc && loc.module === 'locationPicker') { // 防止其他应用也会向该页面post信息，需判断module是否为'locationPicker'
                    console.log('location', loc)
                    // this.$emit('location', loc)
                    setStore("lng",loc.latlng.lng.toFixed(6));
                    setStore("lat",loc.latlng.lat.toFixed(6));
                    // sessionStorage.setItem("lng",loc.latlng.lng.toFixed(6));
                    // sessionStorage.setItem("lat",loc.latlng.lat.toFixed(6));
                    this.geolocation_latlng(loc.latlng.lat.toFixed(6),loc.latlng.lng.toFixed(6));

                }
            },
            geolocation_latlng(lat,lng){
                var coord=new qq.maps.LatLng(lat,lng);
                var geocoder = new qq.maps.Geocoder({
                    complete:function(result){
                        console.log(result.detail.addressComponents);
                        let addressInfo = result.detail.addressComponents;
                        let area = addressInfo.province + addressInfo.city + addressInfo.district ;

                        setStore("area",area);
                        setStore("city",addressInfo.city);
                        setStore("address",addressInfo.town+addressInfo.street+addressInfo.streetNumber);

                        // sessionStorage.setItem("area",area);
                        // sessionStorage.setItem("city",addressInfo.city);
                        // sessionStorage.setItem("address",addressInfo.town+addressInfo.street+addressInfo.streetNumber);
                        history.go(-1);
                    }
                });
                geocoder.getAddress(coord);
            }
        },
        mounted () {
            window.addEventListener('message', this.messageListener, false);
            let divheight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;

            this.$set(this.divheight,'height',divheight);

            // this.divheight = divheight;
            // this.$refs.mapdiv.style.height = h+'px';

        },
        destroyed () {
            window.removeEventListener('message', this.messageListener)
        },data(){
            return{
                divheight:{'height':0},
            }
        }
    }
</script>
<style scoped>
	.map{
		width: 100%;
		overflow: hidden;
	}
</style>



