<template>
<div class="set">
  <div class="title">
    <img @click="back" src="../assets/images/header/back.png" alt="">
    <h2>设置</h2>
    <span @click="edit">完成</span>
  </div>
  <div class="user">
    <router-link to="/photo/head">
      <div class="header">
        <label for="">头像</label>
        <div><img :src="require('../assets/images/set/header.png')" alt=""></div>
      </div>
    </router-link>
    <section class="name">
      <label for="">姓名</label>
      <input @focus="focusName" type="text" name="" v-model="userMsg.realname" readonly="readonly">
    </section>
    <section class="name">
      <label for="">年龄</label>
      <input @focus="focusAge" type="text" name="" v-model="userMsg.age" readonly="readonly">
    </section>
    <section class="sex" @click="sexChange">
      <label for="">性别</label>
      <span v-if="userMsg.gender == 1">男</span>
      <span v-else>女</span>
    </section>
  </div>
  <div class="msg">
    <section class="name">
      <label for="">联系方式</label>
      <input @focus="focusTel" @blur="blurTel" type="text" name="" v-model="userMsg.phone" readonly="readonly">
    </section>
    <section class="name">
      <label for="">身份证号</label>
      <input @focus="focusId" type="text" name="" v-model="userMsg.id_card" readonly="readonly">
    </section>
    <section class="editPwd">
      <label for="">修改密码</label>
    </section>
  </div>
  <div class="btn">退出登录</div>
</div>
</template>

<script>
export default {
  name: 'set',
  data() {
    return {
      userMsg: '',
      show: false,
      oddSex: '',
      imageUrl: '',
      data: ''
    }
  },
  methods: {
    back: function() {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/set.scss';
</style>



