<template>
  <div class="setUpShop3">
    <div class="box">
      <van-cell-group>
        <van-field id="username" @touchstart.native.stop="show = true;status = true"
          v-model="number"
          label="账号"
        />
        <van-field id="username"
          v-model="khh"
          label="开户行"
        />
        <van-field id="username"
          v-model="user"
          label="收款人"
        />
      </van-cell-group>
    </div>
    <div class="box">
      <van-cell-group>
        <van-field id="username" @touchstart.native.stop="show = true;status = false"
          v-model="tel"
          label="银行预留手机号"
        />
      </van-cell-group>
    </div>
    <p><img :src="indexActive" alt="">慧兰医疗保障您的信息安全</p>
    <button type="button" name="button" :class="next ? 'next' : 'none'" @click="routerNext">提交</button>
    <van-number-keyboard
      :show="show"
      extra-key="."
      close-button-text="完成"
      @blur="show = false"
      @input="onInput"
      @delete="onDelete"
    />
  </div>
</template>

<script type="text/javascript">
  import {
      setStore,
      getStore,removeStore
  } from '../config/mUtils'

  import {
    indexActive
  } from "../assets/base64.js"
  import {
    addShopInfo
  } from "../service/getData"
  import {
      Toast
  } from 'vant';
  export default {
    data() {
      return {
        khh: '',
        number: '',
        user: '',
        tel: '',
        indexActive: indexActive(),
        next: false,
        show: false,
        status: true
      }
    },
    mounted: function () {
      let initData = getStore('reginfo') ? JSON.parse(getStore('reginfo')) : '';
        if(initData.bank_place){
          this.khh = initData.bank_place
          this.number = initData.bank_no;
          this.user = initData.shop_user_name;
          this.tel = initData.tel;
        }
    },
    methods: {
      goNext() {
        let panduan = true
        for (var key in this._data) {
          if (this._data[key] === '') {
            panduan = false
          }
        }
        this.next = panduan
      },
      routerNext() {
        if (this.next) {
          let tmp = JSON.parse(getStore("reginfo"))
          tmp.bank_place = this.khh
          tmp.bank_no = this.number
          tmp.shop_user_name = this.user
          tmp.tel = this.tel
          setStore('reginfo',JSON.stringify(tmp))
          addShopInfo(getStore("reginfo")).then(res => {
              if (res.code == 200) {
                  removeStore('reginfo')
                  that.$router.push({
                      name: 'setUpShop3'
                  })
              } else {
                  Toast(res.msg);
              }
          })
        }
      },
      onInput(value) {
        if (this.status) {
          this.number += value
        } else {
          this.tel += value
        }
      },
      onDelete() {
        if (this.status) {
          this.number = ''
        } else {
          this.tel = ''
        }
      }
    },
    watch: {
      khh: function() {
        this.goNext()
      },
      number: function() {
        this.goNext()
      },
      user: function() {
        this.goNext()
      },
      tel: function() {
        this.goNext()
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/setUpShop2.scss';
</style>



