import {post,fetch,patch,put} from '../config/http'
import {baseUrl,userUrl} from '../config/env.js'
/**
 * 获取验证码
 */
export const sendCode = (phone) => {
    return fetch(userUrl+'/sms/sendCode', {
        phone: phone,
        sendType: 1,
        userType: 2
    });
}
/**
 * 登陆
 */
export const login = (phone, verify) => {
    return fetch(userUrl+'/user/login', {
        phone: phone,
        verify: verify,
        sms_type: 1,
        type: 2
    });
}
/**
 * 登出
 */
export const logout = (token) => {
    return post(baseUrl+'/nurse/logout', {
        token: token
    });
}

/**
 * 根据订单状态获取订单
 */
export const getOrders = (status, page, token,shop_id) => {
    return fetch(baseUrl+'/order/getShopOrders', {
        status: status,
        page: page,
        pageRows: 15,
        token: token,
        shop_id:shop_id
    });
}
/**
 * 根据订单编号获取订单信息
 */
export const getOrderDetail = (order_id, token) => {
    return fetch(baseUrl+'/order/getOrderDetail', {
        order_id: order_id,
        token: token
    });
}

/**
 * 根据订单编号获取订单信息
 */
export const getSendDetail = (order_id , token , status) => {
    return fetch(baseUrl+'/order/getOrderDetail', {
        order_id: order_id,
        token: token,
        status:status
    });
}

/**
 * 根据用户类型、用户Id获取消息数据
 */
export const getMsgs = (token, page) => {
    return fetch(baseUrl+'/message/getShopMsgs', {
        token: token,
        page: page,
        pageRows: 10
    });
}

/**
 * 根据用户类型、用户Id删除消息数据
 */
export const delMsgs = (token, msgIds) => {
    return post(baseUrl+'/message/delShopMsgs', {
        token: token,
        msgIds: msgIds
    });
}
/**
 * 根据消息Id标记是否已读未读
 */
export const changeStatus = (token, msgIds) => {
    return post(baseUrl+'/message/changeStatus', {
        token: token,
        msgIds: msgIds
    });
}

/**
 * 头像上传
 */
export const headUpload = (upfile, shop_id) => {
    return post(baseUrl+'/shop/headerImgUpload', {
        shop_id:shop_id,
        upfile: upfile
    });
}

/**
 * 获取护士列表
 */
export const nurseList = (shop_id,nurse_id,token) => {
    return fetch(baseUrl+'/nurse/nurseList', {
        shop_id:shop_id,
        nurse_id:nurse_id,
        token:token
    });
}

/**
 * 获取第三方机构列表
 */
export const shopList = (order_id,token) => {
    return fetch(baseUrl+'/shop/getThirdShops', {
        order_id:order_id,
        token:token
    });
}

/**
 * 获取护士列表
 */
export const confirmPaidan = (orderId , nurse_id, token, shop_id) => {
    return post(baseUrl+'/order/confirmPaidan', {
        orderId:orderId,
        nurse_id:nurse_id,
        turn_shop_id:shop_id,
        token:token
    });
}

/**
 * 根据用户id获取用户店铺
 */
export const getShopList = (token) => {
    return fetch(baseUrl+'/shop/getList',{
        token:token
    });
}

/**
 * 添加店铺
 */
export const addShopInfo = (data) => {
    return post(baseUrl+'/shop/addInfo', data);
}

/**
 * 获取微信配置项
 */
export const getWxConfig = (token , backUrl) => {
    return fetch(baseUrl+'/wx/getWxConfig', {
        token:token,
        gobackurl:backUrl
    });
}

/**
 * 获取城市列表
 */
export const getCityList = () => {
    return fetch(userUrl+'/city/getAll', {
    });
}

/*
 * 获取店铺详情
 */
export const geShopDetail = (token , shop_id) => {
    return fetch(baseUrl+'/shop/getInfoById', {
        token:token,
        shop_id:shop_id
    });
}

/**
 * 根据店铺ID查询店铺团队成员
 */
export const geShopNurse = (token , shop_id) => {
    return fetch(baseUrl+'/shopNurse/getList', {
        token:token,
        shop_id:shop_id
    });
}


