<template>
    <div class="chooseNurse">
        <mOFheader title="分派订单"></mOFheader>
        <div class="title">
            选择服务护士
        </div>
        <ul class="nurse-list">
            <li v-for="item in list" :nurseid = "item.id" @click="selectNurse(item.id)" :class="select_nurse_id == item.id ? 'on' : ''">
                <div>{{item.realname}}</div>
            </li>
        </ul>
        <div class="title">
            选择第三方机构
        </div>
        <ul class="nurse-list">
            <li v-for="item in shops" :nurseid = "item.id" @click="selectShop(item.id)" :class="select_shop_id == item.id ? 'on' : ''">
                <div>{{item.shop_name}}</div>
            </li>
        </ul>
        <div class="submit-button" @click="submitNurse">确认</div>
    </div>
</template>

<script>
    import mOFheader from '../components/mOFheader.vue'
    import {Toast} from "vant";
    
    import {
        nurseList,
        shopList,
        confirmPaidan
    } from '../service/getData'

    export default {
        name: 'chooseNurse',
        data() {
            return {
                list:[],
                shops:[],
                order_id:0,
                shop_id:0,
                nurse_id:0,
                select_nurse_id:0,
                select_shop_id:0,
            }
        },
        mounted: function() {
            this.shop_id = this.$route.params.shop_id
            this.nurse_id = this.$route.params.nurse_id
            this.order_id = this.$route.params.order_id
            nurseList(this.shop_id,this.nurse_id,this.$store.state.userId).then(res => {
                this.list = res.data;
            })
            shopList(this.order_id,this.$store.state.userId).then(res => {
                this.shops = res.data;
            })
        },
        methods: {
            selectNurse(id){
                this.select_nurse_id = id;
                this.select_shop_id = 0
            },
            selectShop(id){
                this.select_nurse_id = 0
                this.select_shop_id= id;
            },
            submitNurse(){
                console.log(this.select_nurse_id);
                console.log(this.select_shop_id);

                if( this.select_nurse_id==0 && this.select_shop_id==0){
                    Toast("请选择护士或者第三方机构");
                    return;
                }
                confirmPaidan(this.order_id , this.select_nurse_id , this.$store.state.userId,this.select_shop_id).then(res => {
                    
                    if(res.code == 200){
                        Toast(res.msg);
                        this.$router.push({
                            name: 'orderList',
                            query: {
                                shop_id:this.shop_id
                            }
                        })
                    }else{
                        Toast(res.msg);
                    }
                })
                //order/confirmPaidan
            }
        },
        components: {
            mOFheader
        }
    }
</script>

<style lang="scss" scoped>
    @import '../assets/css/chooseNurse.scss';
</style>



