import Vue from 'vue'
import Router from 'vue-router'
import nonShop from '@/pages/nonShop' //无店铺
import setUpShop1 from '@/pages/setUpShop1' //新店铺1
import setUpShop2 from '@/pages/setUpShop2' //新店铺2
import setUpShop3 from '@/pages/setUpShop3' //新店铺3
import register1 from '@/pages/register1' //第三方注册1
import register2 from '@/pages/card' //第三方注册2
import register3 from '@/pages/register3' //第三方注册3
import setAccount from '@/pages/setAccount' //第三方注册填写账号
import xbCenter from '@/pages/xbCenter' //我的
import xbSet from '@/pages/xbSet' //设置
import footer from '@/pages/footer' //底部nav
import index from '@/pages/index' //首页
import shopDetail from '@/pages/shopDetail' //店铺详情
import shopEdit from '@/pages/shopEdit' //店铺编辑
import income from '@/pages/income' //收入
import PresentRecord from '@/pages/PresentRecord' //提现记录
import withdrawDeposit from '@/pages/withdrawDeposit' //申请提现
import SubmittedSuccessfully from '@/pages/SubmittedSuccessfully' //提现成功
import chioceNurse from '@/pages/chioceNurse' //选择护士
import AddServiceItems from '@/pages/AddServiceItems' //添加服务项目
import templet from '@/pages/templet' //模板
import addItemSuccess from '@/pages/addItemSuccess' //成功
import addShopAddress from '@/pages/addShopAddress' //地图

import server from '@/pages/server'
import cancel from '@/pages/cancel'
import success from '@/pages/success'
import login from '@/pages/login' //登录

import orderList from '@/pages/orderList'
import sendOrder from '@/pages/sendOrder'
import chooseNurse from '@/pages/chooseNurse'
import message from '@/pages/message'


import shopIcon from '@/pages/photoIcon'
import selectCity from '@/pages/selectCity'

Vue.use(Router)

export default new Router({
  routes: [{ //小b新增
    path: '/',
    component: footer,
    children: [{
      path: '/',
      name: 'index',
      component: index
    }]
  }, {
    path: '/nonShop',
    name: 'nonShop',
    component: nonShop
  }, {
    path: '/setUpShop1',
    name: 'setUpShop1',
    component: setUpShop1
  }, {
    path: '/setUpShop2',
    name: 'setUpShop2',
    component: setUpShop2
  }, {
    path: '/setUpShop3',
    name: 'setUpShop3',
    component: setUpShop3
  }, {
    path: '/register1',
    name: 'register1',
    component: register1
  }, {
    path: '/register2',
    name: 'register2',
    component: register2
  }, {
    path: '/register3',
    name: 'register3',
    component: register3
  }, {
    path: '/setAccount',
    name: 'setAccount',
    component: setAccount
  }, {
    path: '/xbCenter',
    name: 'xbCenter',
    component: xbCenter
  }, {
    path: '/xbSet',
    name: 'xbSet',
    component: xbSet
  }, {
    path: '/shopDetail/:shop_id',
    name: 'shopDetail',
    component: shopDetail
  }, {
    path: '/shopEdit',
    name: 'shopEdit',
    component: shopEdit
  }, {
    path: '/income',
    name: 'income',
    component: income
  }, {
    path: '/PresentRecord',
    name: 'PresentRecord',
    component: PresentRecord
  }, {
    path: '/SubmittedSuccessfully',
    name: 'SubmittedSuccessfully',
    component: SubmittedSuccessfully
  }, {
    path: '/chioceNurse',
    name: 'chioceNurse',
    component: chioceNurse
  }, {
    path: '/templet',
    name: 'templet',
    component: templet
  }, {
    path: '/addItemSuccess',
    name: 'addItemSuccess',
    component: addItemSuccess
  }, {
    path: '/AddServiceItems',
    name: 'AddServiceItems',
    component: AddServiceItems
  }, {
    path: '/withdrawDeposit',
    name: 'withdrawDeposit',
    component: withdrawDeposit
  }, {
    path: '/server/:order_no',
    name: 'server',
    component: server //服务中
  }, {
    path: '/cancel/:order_no',
    name: 'cancel',
    component: cancel //取消订单
  }, {
    path: '/success/:order_no',
    name: 'success',
    component: success //服务完成
  }, {
    path: '/login', //登录
    name: 'login',
    component: login
  }, {
    path: '/orderList', //派发订单的订单列表
    name: 'orderList',
    component: orderList,
  }, {
    path: '/sendOrder/:order_no', //发送订单
    name: 'sendOrder',
    component: sendOrder,
  }, {
    path: '/chooseNurse/:order_id/:shop_id/:nurse_id', //选择护士
    name: 'chooseNurse',
    component: chooseNurse,
  }, {
    path: '/addShopAddress', //地图
    name: 'addShopAddress',
    component: addShopAddress
  }, {
    path: '/shopIcon', //店铺头像
    name: 'shopIcon',
    component: shopIcon
  }, {
    path: '/selectCity', //店铺头像
    name: 'selectCity',
    component: selectCity
  }, {
      path: '/message',//消息
      name: 'message',
      component: message,
  }]
})



