<template>
  <div class="setUpShop3">
    <div class="box">
      <van-cell-group>
        <van-field
          type="tel"
          v-model="number"
          label="账号"
        />
        <van-field
          v-model="khh"
          label="开户行"
        />
        <van-field
          v-model="user"
          label="收款人"
        />
        <van-field
          type="tel"
          v-model="tel"
          label="银行预留手机号"
        />
      </van-cell-group>
    </div>
    <p><img :src="indexActive" alt="">慧兰医疗保障您的信息安全</p>
    <button type="button" name="button" :class="next ? 'next' : 'none'" @click="routerNext">提交</button>

  </div>
</template>

<script type="text/javascript">
  import {
      setStore,
      getStore
  } from '../config/mUtils'

  import {
    indexActive
  } from "../assets/base64.js"
  import {
    addShopInfo
  } from "../service/getData"
  import {
      Toast
  } from 'vant';
  export default {
    data() {
      return {
        khh: '',
        number: '',
        user: '',
        tel: '',
        indexActive: indexActive(),
        next: false,
        show: false,
        status: true
      }
    },
    methods: {
      goNext() {
        let panduan = true
        for (var key in this._data) {
          if (this._data[key] === '') {
            panduan = false
          }
          // if(key == 'tel'){
          //     var reg= /^((13|14|15|17|18|19)[0-9]{1}\d{8})$/;
          //     if(!reg.test(this._data[key])){
          //         panduan = false
          //     }
          // }
        }
        this.next = panduan;
      },
      routerNext() {
        if (this.next) {
            let that = this;
            let info = {
                token:localStorage.userId,
                shop_name:getStore('shop_name'),
                shop_intro:getStore('shop_intro'),
                city_name:getStore('city'),
                address:getStore('address'),
                contacts:that.user,
                contact_phone:that.tel,
                bank_no:that.number,
                shop_user_name:that.user,
                bank_place:that.khh,
                lng:getStore('lng'),
                lat:getStore('lat'),
                logo_path:'',
                shop_type:1,
            };
            addShopInfo(info).then(res => {
                if (res.code == 200) {
                    setStore("area",'');
                    setStore("address",'');
                    setStore("shop_name",'');
                    setStore("shop_intro",'');
                    setStore("lng",'');
                    setStore("lat",'');
                    // sessionStorage.setItem('area','');
                    // sessionStorage.setItem('address','');
                    // sessionStorage.setItem('shop_name','');
                    // sessionStorage.setItem('shop_intro','');
                    // sessionStorage.setItem('lng','');
                    // sessionStorage.setItem('lat','');
                    that.$router.push({
                        name: 'setUpShop3'
                    })
                } else {
                    Toast(res.msg);
                }
            })
        }
      },
      onInput(value) {
        if (this.status) {
          this.number += value
        } else {
          this.tel += value
        }
      },
      onDelete() {
        if (this.status) {
          this.number = ''
        } else {
          this.tel = ''
        }
      }
    },
    watch: {
      khh: function() {
        this.goNext()
      },
      number: function() {
        this.goNext()
      },
      user: function() {
        this.goNext()
      },
      tel: function() {
        this.goNext()
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/setUpShop2.scss';
</style>



