/**
 * 配置编译环境和线上环境之间的切换
 *
 * baseUrl: 域名地址
 * routerMode: 路由模式
 * imgBaseUrl: 图片所在域名地址
 *
 */

let baseUrl = '';
let userUrl = '';
let routerMode = 'hash';
let appUrl = 'http://www.zpdyht.com'; 

if (process.env.NODE_ENV == 'development') {
	baseUrl = 'http://www.zpdyht.com/xbapi/v1';
	userUrl = 'http://www.zpdyht.com/hlyluser/v1'

} else if (process.env.NODE_ENV == 'production') {
  	
  if(document.domain == 'www.dkzpd.com'){
		baseUrl = 'http://www.dkzpd.com/xbapi/v1';
		userUrl = 'http://www.dkzpd.com/hlyluser/v1'
	}else{
		baseUrl = 'http://www.zpdyht.com/xbapi/v1';
		userUrl = 'http://www.zpdyht.com/hlyluser/v1'
	} 
  
}

export {
  baseUrl,
  userUrl,
  appUrl,
  routerMode,
}



