</template>

<script>
  import {geShopDetail, geShopNurse} from '../service/getData'
  import {
      Toast
  } from 'vant';
  import {
    Dialog
  } from 'vant';
  import {
    edit,
    shouru,
    yjt,
    cha,
    add
  } from "../assets/base64.js"
  export default {
    name: 'set',
    data() {
      return {
        edit: edit(),
        shouru: shouru(),
        yjt: yjt(),
        cha: cha(),
        add: add(),
        active: true,
        show: false,
        shopDetail:'',
        shopNurse:''
      }
    },
    methods: {
      onClose(clickPosition, instance) {
        switch (clickPosition) {
          case 'right':
            Dialog.alert({
              title: '该服务有未完成订单',
              message: '请完成或取消后再删除'
            }).then(() => {
              instance.close();
            });
            break;
        }
      },
      close() {
        this.show = false
      }
    },
    mounted() {
        let _self = this;
        geShopDetail(localStorage.userId,this.$route.params.shop_id).then(res => {
            console.log(res);
            if (res.code == 200) {
                _self.shopDetail = res.data;
            } else {
                Toast(res.msg);
            }
        });
        geShopNurse(localStorage.userId,this.$route.params.shop_id).then(res => {
            console.log(res);
            if (res.code == 200) {
                _self.shopNurse = res.data;
            } else {
                Toast(res.msg);
            }
        });
    }
  }
</script>

<style lang="scss">
  .van-cell-swipe__right {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    background-color: #ff3b30;
    border-radius: 0px 16px 16px 0px;
    font-family: PingFang-SC-Medium;
    font-size: 32px;
    color: #ffffff;
  }

  .van-dialog {
