  }

  .van-dialog__header {
    font-family: PingFang-SC-Bold;
    font-size: 36px;
    color: #333333;
    padding-top: 44px;
  }

  .van-dialog__message {
    font-family: PingFang-SC-Medium;
    font-size: 26px;
    color: #333333;
    text-align: center;
  }

  .van-dialog__footer {
    height: 84px;
    line-height: 84px;
  }

  .van-button__text {
    font-family: PingFang-SC-Medium;
    font-size: 32px;
    color: #8977f6;
  }
</style>

<style lang="scss" scoped>
  @import '../assets/css/shopDetail.scss';
</style>



