<template>
  <div class="income">
    <img src="../assets/images/shop.png" alt="">
    <h2>您当前无店铺</h2>
    <p>邀请护士出订单，自己开店做老板！赶紧注册一个属于自己的店铺吧~</p>
    <div class="buttonBox">
      <button @click="goSetUpShop" type="button" name="button">立即开店</button>
      <button @click="goRegister" type="button" name="button">第三方机构注册</button>
    </div>
  </div>
</template>

<script type="text/javascript">

  import {
      setStore,
      getStore
  } from '../config/mUtils'

  export default {
    methods: {
      goRegister: function() {
        this.$router.push({
          name: 'register1'
        })
      },
      goSetUpShop: function () {
          setStore("area",'');
          setStore("address",'');
          setStore("shop_name",'');
          setStore("shop_intro",'');
        // sessionStorage.setItem('area','');
        // sessionStorage.setItem('address','');
        // sessionStorage.setItem('shop_name','');
        // sessionStorage.setItem('shop_intro','');
        this.$router.push({
          name: 'setUpShop1'
        })
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/nonShop.scss';
</style>



