<template>
  <div class="withdrawDeposit">
    <div class="main">
      <h2>提现金额</h2>
      <div class="inputBox">
        <span>￥</span><input type="number" v-model="txje">
      </div>
      <p v-if="txje === ''"><span>可提现金额 {{jine}}元</span><i @click="qb">全部提现</i></p>
      <p v-else><span>剩余可提现金额 {{jine - txje}}元</span><i @click="qb">全部提现</i></p>
    </div>
    <button type="button" name="button" :class="wait === true ? 'wait' : ''">申请提现</button>
  </div>
</template>

<script type="text/javascript">
  export default {
    data() {
      return {
        jine: 368,
        txje: '',
        wait: true
      }
    },
    methods: {
      qb() {
        this.txje = this.jine
      }
    },
    watch: {
      txje: function (e) {
        console.log(e);
        if(e !== ''){
          this.wait = false
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/withdrawDeposit.scss';
</style>



