<template>
  <div class="footer">
    <router-view></router-view>
    <van-tabbar v-model="active">
      <van-tabbar-item to="/message">
        <span>消息</span>
        <template slot="icon" slot-scope="props">
          <div>
            <img class="tuijianma" :src="props.active ? icon.msgActive : icon.msg" />
          </div>
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/">
        <span>首页</span>
        <template slot="icon" slot-scope="props">
          <img :src="props.active ? icon.indexActive : icon.index" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/xbCenter">
        <span>我的</span>
        <template slot="icon" slot-scope="props">
          <img class="mine" :src="props.active ? icon.mineActive : icon.mine" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import {
  indexActive,
  index,
  msg,
  msgActive,
  mine,
  mineActive
} from "../assets/base64.js"
  export default {
    name: 'footer',
    data() {
      return {
        active: 1,
        icon: {
          indexActive: indexActive(),
          index: index(),
          msg: msg(),
          mine: mine(),
          mineActive: mineActive(),
          msgActive: msgActive()
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/footer.scss';
</style>



