const getStore = name => {
	if (!name) return;
	return window.localStorage.getItem(name);
}

let userinfo;
let userinfo_state = {
	userId:'',
	userinfo:{},
};

if( getStore('userinfo') != "undefined" ){
	userinfo = getStore('userinfo');
	userinfo_state.userinfo = JSON.parse(userinfo)  
}

const state = userinfo_state;

export default state



