<template>
    <div class="register1">
        <div class="box">
            <van-cell-group>
                <van-field
                    v-model="mechanism"
                    label="机构名称"
                />
<!--                 <van-field 
                    v-model="user"
                    label="联系人"
                />
                <van-field type="tel" @touchstart.native.stop="show = true"
                    v-model="tel"
                    label="银行预留手机号"
                /> -->
            </van-cell-group>
        </div>
        <div class="h2" @click="selectCitys"><div>服务提供城市<span>（可多选）</span></div><img :src="yjt" alt=""></div>
        <div class="citys">
            <span v-for="(city,index) in citys">{{city}}</span>
        </div>
        <div class="h2">公司地址</div>
        <textarea class="address" name="name" v-model="address"></textarea>
        <textarea class="miaoshu" name="name" v-model="miaoshu" placeholder="点击添加店铺描述..."></textarea>
        <button type="button" name="button" :class="next ? 'next' : 'none'" @click="routerNext">下一步</button>
        <van-number-keyboard
            :show="show"
            extra-key="."
            close-button-text="完成"
            @blur="show = false"
            @input="onInput"
            @delete="onDelete"
        />
    </div>
</template>

<script type="text/javascript">
    import {
        yjt
    } from "../assets/base64.js"
    import {
        setStore,getStore
    } from "../config/mUtils.js"
    export default {
        data() {
            return {
                mechanism: '',
                address: '',
                citys: [],
                miaoshu: '',
                next: false,
                show: false,
                yjt: yjt(),
            }
        },
        mounted: function () {
            let initData = getStore('reginfo') ? JSON.parse(getStore('reginfo')) : '';
            if(initData){
                this.mechanism = initData.shop_name
                this.city = initData.city_name
                this.address = initData.address
                this.miaoshu = initData.shop_intro
                this.zhiye = initData.zhiye
            }

            var city_list = getStore("selectCity");
                if(city_list){
                    this.citys = city_list.split("-");
                }
        },
        methods: {
            goNext() {
                let panduan = true
                for (var key in this._data) {
                    if (this._data[key] === '') {
                        panduan = false
                    }
                }
                this.next = panduan
            },
            routerNext() {
                if (this.next) {
                    let reginfo = {
                        'shop_name' : this.mechanism,
                        'city_name' : '南京',
                        'address' : this.address,
                        'shop_intro': this.miaoshu,
                        'zhiye': this.zhiye,
                        'shop_type' : 2,
                        'token': localStorage.userId
                    }
                    setStore('reginfo',JSON.stringify(reginfo))
                    this.$router.push({
                        name: 'register2'
                    })
                }
            },
            selectCitys(){
                this.$router.push({
                    name: 'selectCity'
                })
            }
        },
        watch: {
            address: function() {
                this.goNext()
            },
            mechanism: function() {
                this.goNext()
            },
            miaoshu: function() {
                this.goNext()
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../assets/css/register1.scss';
</style>



