<template>
    <div class="select-city-box">
        
        <div class="now-select-box">
            <div class="title-box">
                <div class="title">当前选择</div>
                <div class="delete-box" @click="clearCitys">
                    清空
                </div>
            </div>

            <ul class="select-list" id="select-list">
                <li v-for="(city,index) in citys">{{city}}</li>
            </ul>
        </div>
        
        <div class="hot-city-box">

            <div class="title" id="Hot">热门城市</div>
            <ul class="city-list" id="hot-city-list">
                <li v-for="(city,index) in hotCities" :key="index" class="list_li"  @click="selectCity(city.city_name)">{{city.city_name}}</li>
            </ul>
        </div>
            
        <div class="city-box">

            <div class="city-list" v-for="(citys,index) in allCities">
                <div class="title" :id="index"> {{index}} </div>
                <ul id="all-city-list">
                    <li v-for="(city,index) in citys" class="list_li" @click="selectCity(city.city_name)">{{city.city_name}}  <img src="@/assets/images/select_city_on.png" /> </li>
                </ul>
            </div>
            
        </div>
        
        <div class="letter-box">
            <div class="letters">
                <div @click="scrollTop('Hot');"> 选择<br/>热门 </div>
                <div v-for="(zm,index) in abc" @click="scrollTop(zm);">{{zm}}</div>
            </div>
        </div>
        
        <div class="submit-box" @click="submitButton">
            确定
        </div>
        
    </div>
</template>

<script type="text/javascript">
   
    import {
        setStore,getStore
    } from "@/config/mUtils.js"

    import {
        getCityList
    } from '@/service/getData'

    export default {
        data() {
            return {
                citys: [],
                abc:[],
                allCities:[],
                hotCities:[]
            }
        },
        mounted: function () {
            
            getCityList().then(res => {
                if (res.code == 200) {
                   
                    this.abc = res.data.abc;
                    this.allCities = res.data.allCities;
                    this.hotCities = res.data.hotCities;
                } else {
                    Toast(res.msg);
                }
            })

            var city_list = getStore("selectCity");
            if(city_list){
                this.citys = city_list.split("-");
            }
        },
        updated:function(){

            var cityLists = document.getElementsByClassName('list_li'); 
            
            for(var i = 0 ; i < cityLists.length ; i++){
                for(var j = 0 ; j < this.citys.length ; j++){

                    if(cityLists[i].innerText == this.citys[j]){
                        cityLists[i].classList.add("on");
                    }
                }
            }
        },
        methods: {
            selectCity:function(city_name){

                var check = false;
                var new_citys = [];

                //判断是否已经添加过
                for(var i = 0 ; i < this.citys.length; i++){
                    if(this.citys[i] == city_name){
                        check = true;
                        continue;
                    }
                    new_citys.push(this.citys[i]);
                }

                this.citys = new_citys;

                var select_list = document.getElementById('select-list'); 

                if(check){

                    //删除选择的城市
                    var lis = select_list.getElementsByTagName("li");

                    for(var i = 0 ; i < lis.length ; i++){

                        if(lis[i].innerHTML == city_name){
                            lis[i].remove();
                        }
                    }
                }else{

                    this.citys.push(city_name);
                }

                var cityLists = document.getElementsByClassName('list_li'); 
            
                for(var i = 0 ; i < cityLists.length ; i++){
                    var has = false;
                    for(var j = 0 ; j < this.citys.length ; j++){

                        if(cityLists[i].innerText == this.citys[j]){
                            has = true;
                        }
                    }

                    if(has){

                        cityLists[i].classList.add("on");
                    }else{

                        cityLists[i].classList.remove("on");
                    }
                }
            },
            scrollTop:function(obj){
                let ele = document.getElementById(obj)
                let eleH = ele.offsetTop;
                window.scrollTo(0,eleH);
            },
            clearCitys:function(){

                this.citys = [];

                var select_list = document.getElementById('select-list'); 
                    select_list.innerHTML = "";

                var cityLists = document.getElementsByClassName('list_li'); 
            
                for(var i = 0 ; i < cityLists.length ; i++){
                    var has = false;
                    for(var j = 0 ; j < this.citys.length ; j++){

                        if(cityLists[i].innerText == this.citys[j]){
                            has = true;
                        }
                    }

                    if(has){

                        cityLists[i].classList.add("on");
                    }else{

                        cityLists[i].classList.remove("on");
                    }
                }
            },
            submitButton:function(){

                setStore("selectCity" , this.citys.join("-"));
                this.$router.push({
                    name: 'register1'
                })
            }
        }
    }
</script>

<style lang="scss">
    @import '../assets/css/selectCity.scss';
</style>



