<template>
  <div class="card">
    <section>
      <h2>请上传机构相关证书</h2>
      <p>1、上传本机构资质证书；</p>
      <p>2、上传的照片请确保身份证边框完整、图像清晰、光线均匀</p>    
      <selectImg title="机构资质证书"  :backUrl="backUrl"  v-on:selectImg="selectZhiYeImg"></selectImg>
      <div class="updateSuccess" hidden>
        <img src="@/assets/images/card.png" alt="">
        <div class="fontBox">
          <img src="@/assets/images/success.png" alt="">
          <p>上传成功</p>
        </div>
      </div>
    </section>
    <p class="tishi"><img :src="indexActive" alt="">慧兰医疗保障您的信息安全</p>
    <button type="button" name="button" :class="next ? 'next' : 'none'" @click="routerNext">下一步</button>
  </div>
</template>
<script src="http://res.wx.qq.com/open/js/jweixin-1.0.0.js"/></script>
<script type="text/javascript">
    import {
      setStore,getStore
    } from "../config/mUtils.js"
    import {appUrl} from '../config/env.js'
    import selectImg from '@/components/selectImg.vue'
    import {
        Toast
    } from 'vant';
  import {
    indexActive
  } from "../assets/base64.js"
  export default {
    data() {
      return {
        indexActive: indexActive(),
        next: false,
        zhiye: '',
        backUrl: appUrl + "/hlylxbmanager/#/register2",
      }
    },
    components: {
        selectImg
    },
    mounted: function () {
      let initData = getStore('reginfo') ? JSON.parse(getStore('reginfo')) : '';
        if(initData.zhiye){
          this.zhiye = initData.zhiye
          this.next = true;
        }
    },
    methods: {
      selectZhiYeImg(a){
          this.zhiye = a.serverId;
          this.goNext();
      },
      routerNext() {
        if (this.next) {
          this.$router.push({
            name: 'setAccount'
          })
        }
      },
      goNext() {
        if (this.zhiye !== '') {
          let tmp = JSON.parse(getStore("reginfo"))
          tmp.zhiye = this.zhiye
          setStore('reginfo',JSON.stringify(tmp))
          this.next = true;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/card.scss';
</style>



