<template>
  <div class="AddServiceItems">
    <section>
      <div class="topBox">
        <img src="@/assets/images/center/01.png" alt="">
        <div class="topRightBox">
          <h2>产后乳汁淤积</h2>
          <p>20~30分钟</p>
        </div>
      </div>
      <div class="bottomBox">
        <van-checkbox v-model="check1" @change="changeStatus">选择</van-checkbox>
        <div class="bottomRightBox">
          <img :src="ljt" alt="">
          <span>删除</span>
        </div>
      </div>
    </section>
    <section>
      <div class="topBox">
        <img src="@/assets/images/center/01.png" alt="">
        <div class="topRightBox">
          <h2>产后乳汁淤积</h2>
          <p>20~30分钟</p>
        </div>
      </div>
      <div class="bottomBox">
        <van-checkbox v-model="check2" @change="changeStatus">选择</van-checkbox>
        <div class="bottomRightBox">
          <img :src="ljt" alt="">
          <span>删除</span>
        </div>
      </div>
    </section>
    <button type="button" name="button" class="zdymb"><img :src="addZi" alt="">自定义模版</button>
    <button type="button" name="button" :class="next ? 'next' : 'none'">确认添加</button>
  </div>
</template>

<script>
  import {
    ljt,
    addZi
  } from "../assets/base64.js"
  export default {
    name: 'AddServiceItems',
    data() {
      return {
        ljt: ljt(),
        addZi: addZi(),
        next: false,
        check1: '',
        check2: '',
      }
    },
    methods: {
      changeStatus() {
        if (this.check1 !== '' || this.check2 !== '') {
          this.next = true
        } else {
          this.next = false
        }
      }
    }
  }
</script>

<style lang="scss">
  .van-checkbox--checked {
    border-color: #8977f6;
    background-color: #8977f6;
  }

  .van-checkbox__icon {
    font-size: 30px;
    width: 40px;
    height: 40px;
    line-height: 40px;
  }

  .van-checkbox__label {
    font-family: PingFang-SC-Medium;
    font-size: 24px;
    color: #cccccc;
  }
</style>

<style lang="scss" scoped>
  @import '../assets/css/AddServiceItems.scss';
</style>



