<template>
    <div class="chioceNurse">
      <van-checkbox-group v-model="result">
        <van-checkbox
          v-for="(item, index) in list"
          :key="item"
          :name="item"
        >
        <img src="@/assets/images/center/01.png" alt="">
        <div class="rightBox">
          <h2>冯程程<img src="@/assets/images/girl.png" alt=""></h2>
          <p>护士号：289012220</p>
        </div>
        </van-checkbox>
      </van-checkbox-group>
      <button type="button" name="button" :class="next ? 'next' : 'none'">确认</button>
    </div>
</template>

<script>
  export default {
    name: 'chioceNurse',
    data() {
      return {
        list: ['a', 'b', 'c'],
        result: ['a', 'b'],
        next: true
      }
    },
    watch: {
      result: function () {
        if (this.result.length !== 0) {
          this.next = true
        } else {
          this.next = false
        }
      }
    }
  }
</script>

<style lang="scss">
  .van-checkbox--checked {
    border-color: #8977f6;
    background-color: #8977f6;
  }

  .van-checkbox__icon {
    font-size: 30px;
    width: 40px;
    height: 40px;
    line-height: 40px;
  }

  .van-checkbox__label {
    margin-left: 50px;
    display: flex;
    border-bottom: 1px solid #efefef;
    flex: 1;
    height: 100%;
    align-items: center;
  }
</style>

<style lang="scss" scoped>
  @import '../assets/css/chioceNurse.scss';
</style>



