<template>
    <div class="myOrderForm">
        <mOFheader title="订单列表" backUrl=""></mOFheader>
       <!--  <nav ref="nav">
            <span :class="nav == '全部'?'active':''" @click="change('全部')">全部</span>
            <span :class="nav == 5?'active':''" @click="change(5)">待确定</span>
            <span :class="nav == 7?'active':''" @click="change(7)">待出诊</span>
            <span :class="nav == 11?'active':''" @click="change(11)">服务中</span>
        </nav> -->
        <section id="index-scroll" ref="wrapper" :class="none == true ? 'none' : ''">
            <ul>
                <div class="refresh">
                    <img src="../assets/images/myOrderForm/loading.gif" alt=""><span>{{top}}...</span>
                </div>
                <li v-for="item in list" @click="details(item.status,item.id)">
                    <h2>
                        <span class="time time2">服务时间：{{item.book_time_text}}</span>
                        <span class="status2">待分派</span>
                    </h2>
                    <section class="detail">
                        <img :src="item.product.logo_path" alt="">
                        <div>
                            <p>患者： <span>{{item.patient_name}}</span></p>
                            <p>服务项目：<span>{{item.product_name}}({{item.num_intro}})</span></p>
                        </div>
                    </section>
                    <section class="btn">
                        <p></p>
                        <div class="btn0" @click.stop="send(item.status,item.id)">去分派</div>
                    </section>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    import {
        getOrders,
    } from '../service/getData'
    import mOFheader from '../components/mOFheader.vue'
    import BScroll from 'better-scroll'
    import Vue from 'vue'
    export default {
        name: 'orderList',
        data() {
            return {
                list: '',
                startY: -50,
                top: '刷新列表中',
                nav: '全部',
                pageNumber: 1,
                none: false,
            }
        },
        components: {
            mOFheader
        },
        mounted: function() {
            let shop_id = this.$route.query.shop_id
            getOrders(3, 1, this.$store.state.userId, shop_id).then(res => {
                this.list = res.data.data;
                this.listScroll();

                if(res.data.data.length == 0){
                    this.none = true;
                }

                // if (this.$route.params.status == '全部') {
                //     this.nav = '全部'
                // } else {
                //     this.nav = parseInt(this.$route.params.status)
                // }
            })
        },
        methods: {
            listScroll: function() {
                var that = this
                Vue.nextTick(() => {
                    that.wrapper = new BScroll(that.$refs.wrapper, {
                        startY: that.startY,
                        click: true
                    })
                    if (that.wrapper.wrapperHeight < that.wrapper.scrollerHeight) {
                        that.add = true
                    }
                    that.top = '刷新列表中'
                    that.wrapper.on('scrollEnd', (pos) => {
                        if (pos.y == 0) {
                            that.pageNumber = 1
                            that.startY = -50
                            getOrders(3, 1, this.$store.state.userId).then(res => {
                                this.list = res.data.data;
                                this.listScroll()
                            })
                        } else if (pos.y == that.wrapper.maxScrollY) {
                            that.startY = that.wrapper.maxScrollY
                            that.pageNumber++
                                getOrders(3, this.pageNumber, this.$store.state.userId).then(res => {
                                    this.list = this.list.concat(res.data.data);
                                    this.listScroll()
                                })
                        }
                    })
                })
            },
           
            details: function(status, order_no) {
                this.$router.push({
                    name: 'sendOrder',
                    params: {
                        order_no: order_no
                    }
                })
            },
            send: function(status, order_no) {
                this.$router.push({
                    name: 'sendOrder',
                    params: {
                        order_no: order_no
                    }
                })
            }
        },
        watch: {
            // nav: function() {
            //     Vue.nextTick(() => {
            //         this.wrapper.scrollTo(0, -50)
            //         if (this.$refs.wrapper.children[0].children.length == 1) {
            //             this.none = true
            //         } else {
            //             this.none = false
            //         }
            //     })
            // }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../assets/css/orderList.scss';
</style>



