<template>
    <div class="toBeSetOut">
        <mOFheader title="订单详情"></mOFheader>
        <div class="logoBox">
            <img src="../assets/images/DDdetail/dpd.png" alt="">
            <div class="banner">
                <p><img :src="main.product.logo_path" alt=""><span>{{main.product.product_name}}</span></p>
                <div class="label">
                    <span>产妇护理包</span>
                    <span>护理员职业责任保险</span>
                </div>
            </div>
        </div>
        <div class="user">
            <section>
                <p><i></i><span>患者姓名</span></p>
                <p>{{main.order_patient.patient_name}}</p>
            </section>
            <section>
                <p><i></i><span>患者年龄</span></p>
                <p>{{main.order_patient.patient_age}}岁</p>
            </section>
            <section>
                <p><i></i><span>患者性别</span></p>
                <p>{{main.order_patient.patient_gender}}</p>
            </section>
            <section>
                <p><i></i><span>是否本院患者</span></p>
                <p v-if="main.order_patient.is_hospital == 1">是</p>
                <p v-else>否</p>
            </section>
        </div>
        <div class="address">
            <section>
                <span>服务时间</span>
                <p>{{main.book_time_text}}</p>
            </section>
            <section>
                <span>服务地点</span>
                <p>{{main.order_address}}</p>
            </section>
            <section>
                <span>病情详情</span>
                <p>{{main.medical_desc}}</p>
            </section>
        </div>
        <div class="btn">
            <a class="btn1" @click="qdpd">确定派单</a>
            <a :href="telNum(main.patient_phone)" class="btn2"><img src="../assets/images/DDdetail/tel.png" alt="">联系患者</a>
        </div>
    </div>
</template>

<script>
    import mOFheader from '../components/mOFheader.vue'
    import {Toast} from "vant";
    
    import {
        getOrderDetail,
        confirmArrive,
        getSendDetail
    } from '../service/getData'
    export default {
        name: 'sendOrder',
        data() {
            return {
                main:{
                    product:{

                    },
                    order_patient:{
                        patient_name:'',
                    }
                }
            }
        },
        mounted: function() {
            getSendDetail(this.$route.params.order_no, this.$store.state.userId , 3).then(res => {
                this.main = res.data
            })
        },
        methods: {
            qdpd(){
                var order_no = this.$route.params.order_no;
                this.$router.push({
                    name: 'chooseNurse',
                    params: {
                        order_no: order_no,
                        shop_id: this.main.shop_id,
                        nurse_id:this.main.nurse_id
                    }
                })
            },
            telNum: (phoneNum) => {
                if (typeof phoneNum !== 'undefined') {
                    return 'tel:' + phoneNum
                } else {
                    return ''
                }
            }
        },
        components: {
            mOFheader
        }
    }
</script>

<style lang="scss" scoped>
    @import '../assets/css/toBeSetOut.scss';
</style>



