<template>
  <div class="income">
    <img src="../assets/images/updateSuccess.png" alt="">
    <h2>提交成功</h2>
    <p>您的提现申请已提交，请耐心等待</p>
    <button @click="userInfo" type="button" name="button">确定</button>
  </div>
</template>

<script type="text/javascript">
  export default {
    methods: {
      userInfo: function() {
        this.$router.push({
          path: '/'
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/updateSuccess.scss';
</style>



