<template>
  <div class="templet">
    <div class="cameraBox">
      <div class="buttoBox">
        <img :src="camera" alt="">
      </div>
      <p>添加商品照片</p>
    </div>
    <div class="user">
      <section class="name">
        <label for="">服务项目名称</label>
        <input type="text" name="" v-model="itemName">
      </section>
      <van-cell @click="hospitalGroup" title="服务分类" is-link :value="activeHospital" />
      <section class="name">
        <label for="">服务项目费用</label>
        <input type="text" name="" v-model="itemName">
      </section>
      <section class="name">
        <label for="">服务时长</label>
        <input type="text" name="" v-model="itemName">
      </section>
    </div>
    <div class="user">
      <van-cell title="服务星期" is-link :value="activeHospital" />
      <section class="name">
        <label for="">服务时间</label>
        <input type="text" name="" v-model="itemName">
      </section>
    </div>
    <div class="user">
      <h2>服务简介</h2>
      <textarea name="name" placeholder="描述服务项目..." v-model="fwjs"></textarea>
      <p>{{fwjs.length}}/200</p>
    </div>
    <div class="user">
      <h2>适用人群</h2>
      <textarea name="name" placeholder="详述适用该服务的人群..." v-model="fwjs"></textarea>
      <p>{{fwjs.length}}/200</p>
    </div>
    <div class="user">
      <h2>禁忌提示</h2>
      <textarea name="name" placeholder="详述此项目的禁忌提示..." v-model="fwjs"></textarea>
      <p>{{fwjs.length}}/200</p>
    </div>
    <button type="button" name="button" class="zdymb"><img :src="addZi" alt="">自定义模版</button>
    <button type="button" name="button" :class="next ? 'next' : 'none'">确认添加</button>
    <van-popup v-model="showhos" position="bottom">
      <van-picker :columns="hospital" :show-toolbar="true" @cancel="cancel" @confirm="onChange" />
    </van-popup>
  </div>
</template>

<script>
  import {
    camera
  } from "../assets/base64.js"

  export default {
    name: 'templet',
    data() {
      return {
        camera: camera(),
        itemName: '',
        hospital: ['服务分类'],
        showhos: false,
        activeHospital: '',
        fwjs: ''
      }
    },
    methods: {
      hospitalGroup() {
          this.showhos = true
      },
      onChange(value) {
          this.activeHospital = value
          this.showhos = false
      },
      cancel() {
          this.showhos = false
      },
    }
  }
</script>

<style lang="scss">
.van-cell__title{
  display: flex;
  align-items: center;
}
.van-cell__value{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 74px;
  color: #999999;
}
.van-cell__right-icon{
  right: 38px;
}
</style>

<style lang="scss" scoped>
  @import '../assets/css/templet.scss';
</style>



