<template>
  <div class="center">
    <div class="bg">
      <div class="title">
        <img @click="back" :src="require('../assets/images/header/back.png')" alt="">
        <img @click="set" :src="require('../assets/images/center/set.png')" alt="">
      </div>
      <img class="header" :src="require('../assets/images/default/140x140.png')" alt="">
      <p class="userPhone">{{userPhone}}</p>
      <!-- <p><span :class="userMsg.gender == 1 ? 'boy' : 'girl'">姜楠</span></p> -->
    </div>
    <div class="czjl">
      <span>我的店铺</span>
    </div>
    <div class="wddd">
      <span>客服电话</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'center',
    data(){
      return{
        userMsg: '',
        userPhone:'',
      }
    },
    mounted: function() {
      let phone = this.$store.state.userinfo.phone;
      this.userPhone =  phone.substr(0, 3) + '****' + phone.substr(7);

    },
    methods: {
      back: function() {
        this.$router.push({
          path: '/'
        })
      },
      set: function() {
        return ;
        this.$router.push({
          name: 'xbSet'
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/center.scss';
</style>



